




















import { Component, Vue } from "vue-property-decorator";
import api from "@/data/apis";
import { APIOwner } from "@/types/api-response";
import { Nullable } from "@/types/nullable";
import { SquareGrid } from "vue-loading-spinner";
import { ProyectosAmigo } from "@/types/stats";

@Component({
  components: {
    SquareGrid,
  },
})
export default class FriendProjects extends Vue {
  owner: Nullable<APIOwner> = null;
  projects: Nullable<ProyectosAmigo[]> = null;
  waiting = true;

  async mounted(): Promise<void> {
    this.loadData();
  }

  async loadData(): Promise<void> {
    const statsData = await api.getStats();
    this.projects = statsData.body["proyectos amigos"];
    this.waiting = false;
  }
}
